"use client";

import { useState, useEffect } from "react";
import { Terminal, Search, Zap, AlertTriangle, CheckCircle2, XCircle, Building2 } from "lucide-react";
import { ResizableHandle, ResizablePanel, ResizablePanelGroup } from "@/components/ui/resizable";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Alert, AlertTitle, AlertDescription } from "@/components/ui/alert";
import { Skeleton } from "@/components/ui/skeleton";
import { Input } from "@/components/ui/input";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";

export default function HomePage() {
  const [estacoes, setEstacoes] = useState([]);
  const [estacaoSelecionada, setEstacaoSelecionada] = useState(null);
  const [pontos, setPontos] = useState([]);
  const [loadingEstacoes, setLoadingEstacoes] = useState(true);
  const [loadingPontos, setLoadingPontos] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [filtro, setFiltro] = useState("");

// …

useEffect(() => {
  async function fetchEstacoes() {
    try {
      const res = await fetch("/api/estacoes");
      const data = await res.json();
      setEstacoes(data);
    } catch (e) {
      setError("Erro ao carregar subestações");
    } finally {
      setLoadingEstacoes(false);
    }
  }
  fetchEstacoes();
}, []);

// --- render -----------------------------------------------------------
return (
  <ResizablePanelGroup direction="horizontal" className="h-screen border">

    {/* SIDEBAR --------------------------------------------------------- */}
    <ResizablePanel defaultSize={25} className="bg-gray-50 border-r">
      {/* Campo de busca */}
      <div className="p-3">
        <div className="relative">
          <Search className="absolute left-2 top-2.5 h-4 w-4 text-gray-400" />
          <Input
            placeholder="Buscar subestação..."
            value={filtro}
            onChange={(e) => setFiltro(e.target.value)}
            className="pl-8"
          />
        </div>
      </div>

      {/* Lista de subestações */}
      <ScrollArea className="h-[calc(100%-60px)] custom-scroll">
        <ul className="space-y-1 px-2">
          {loadingEstacoes
            ? Array(8)
                .fill(0)
                .map((_, i) => <Skeleton key={i} className="h-6 w-full" />)
            : estacoes
                .filter(
                  (e) =>
                    e.nome.toLowerCase().includes(filtro.toLowerCase()) ||
                    e.codigo.toLowerCase().includes(filtro.toLowerCase())
                )
                .map((e) => (
                  <li
                    key={e.codigo}
                    onClick={() => setEstacaoSelecionada(e)}
                    className={`flex items-center gap-2 cursor-pointer rounded-md px-3 py-2 text-sm transition-colors ${
                      estacaoSelecionada?.codigo === e.codigo
                        ? "bg-blue-100 text-blue-700 font-medium"
                        : "hover:bg-gray-100 text-gray-700"
                    }`}
                  >
                    <Building2 className="h-4 w-4 text-gray-500" />
                    <span>
                      {e.codigo}: {e.nome}
                    </span>
                  </li>
                ))}
        </ul>
      </ScrollArea>
    </ResizablePanel>

    <ResizableHandle />

    {/* CONTEÚDO -------------------------------------------------------- */}
    <ResizablePanel>
      <div className="p-4">
        {estacaoSelecionada ? (
          <>
            <h1 className="text-xl font-bold mb-2">
              {estacaoSelecionada.nome}
            </h1>
            <p className="text-sm text-gray-500 mb-4">
              Código: {estacaoSelecionada.codigo}
            </p>

            <div className="border rounded-lg overflow-hidden shadow-sm">
              <ScrollArea className="max-h-[75vh] custom-scroll">
                <Table>
                  <TableHead className="bg-gray-100 sticky top-0 z-10">
                    <TableRow>
                      <TableHead className="w-[250px]">ID do Ponto</TableHead>
                      <TableHead>Descrição</TableHead>
                      <TableHead className="w-24">Status</TableHead>
                      <TableHead className="text-right w-24">
                        N° Ponto
                      </TableHead>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {pontos.map((p, i) => (
                      <TableRow
                        key={p.id}
                        className={i % 2 ? "bg-gray-50" : "bg-white"}
                      >
                        <TableCell className="font-mono text-xs">
                          {p.id}
                        </TableCell>
                        <TableCell className="text-sm">
                          {p.descricao}
                        </TableCell>
                        <TableCell>{/* status aqui */}</TableCell>
                        <TableCell className="text-right">
                          {p.nPonto}
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </ScrollArea>
            </div>
          </>
        ) : (
          <div className="flex h-full items-center justify-center">
            <Alert className="max-w-md">
              <Terminal className="h-4 w-4" />
              <AlertTitle>Nenhuma subestação selecionada</AlertTitle>
              <AlertDescription>
                Selecione uma subestação na barra lateral.
              </AlertDescription>
            </Alert>
          </div>
        )}
      </div>
    </ResizablePanel>
  </ResizablePanelGroup>
);
}
